
import {
  ADD_HEADSET,
  ADD_HEADSET_SUCCESS,
  SHOW_ADD_VIDEO,
  HIDE_ADD_VIDEO,
  LIST_HEADSETS_ALL,
  LIST_HEADSETS_ALL_SUCCESS,
  UPLOAD_VIDEO,
  DELETE_VIDEO,
  LIST_HEADSETS_VIDEOS,
  LIST_HEADSETS_VIDEOS_SUCCESS,
  LIST_VIDEOS_ALL,
  LIST_VIDEOS_ALL_SUCCESS,
  REMOVE_HEADSET,
  DROP_VIDEO_THUMBNAIL,
  UPLOAD_VIDEO_THUMBNAIL,
  ADD_VIDEO_IN_HEADSET,
  PLAY_VIDEO,
  TEST_FIRE
} from "../actions";


const INIT_STATE = {
  headsets: {},
  videos: {},
  videotoUpload: {},
  loading: false,
  vLoading: false,
  addVideoModal: false
};

const reducer = (state = INIT_STATE, action) => {
  // console.log("loading: " + state.loading);
  console.log(action)
  switch (action.type) {
    case LIST_HEADSETS_ALL:
      return {
        ...state,
        loading: true
      };
    case SHOW_ADD_VIDEO: 
      return{
        ...state,
          addVideoModal : true
      }
    case HIDE_ADD_VIDEO: 
      return{
        ...state,
          addVideoModal : false
      }
    case LIST_HEADSETS_ALL_SUCCESS:
      return {
        ...state,
        loading: false,
        headsets: action.payload
      };
    case UPLOAD_VIDEO:
      var newVids = state.headsets[action.payload.videoInfo.headsetID].numberVideos;
      newVids++;
      return {
        ...state,
          headsets: {
            ...state.headsets,
              [action.payload.videoInfo.headsetID] : {
                ...state.headsets[action.payload.videoInfo.headsetID],
                  numberVideos: newVids 
              }
          }
      }
      case DELETE_VIDEO: 
        var decVids = state.headsets[action.payload.headsetID].numberVideos;
        decVids--;
        return {
          ...state,
            headsets: {
              ...state.headsets,
                [action.payload.headsetID] : {
                  ...state.headsets[action.payload.headsetID],
                    numberVideos: decVids
                }
            }
        }
    case LIST_VIDEOS_ALL:
      return {
        ...state,
        vLoading: true
      };
    case LIST_VIDEOS_ALL_SUCCESS:
      return {
        ...state,
        vLoading: false,
        videos: action.payload
      };
    case REMOVE_HEADSET:
      return {
        ...state,
        loading: true
      };
    case LIST_HEADSETS_VIDEOS:
      return {
        ...state,
        vLoading: true
      };
    case LIST_HEADSETS_VIDEOS_SUCCESS:
      return {
        ...state,
        vLoading: false,
        videos: {
          ...state.videos,
          [action.payload.headsetID]: action.payload.videos
        }
      };

    default:
      return { ...state };
  }
};

export default reducer;
