import {
  all,
  call,
  fork,
  put,
  takeEvery
} from "redux-saga/effects";
import {
  database,
  storage
} from "../../helpers/Firebase";
import {
  onHeadsetAddedSuccess,
  onHeadsetAllListed,
  onHeadsetAllListedSuccess,
  onVideoAllListedSuccess,
  onVideoAdded,
  HIDE_ADD_VIDEO,
  onHeadsetListVideo,
  onHeadsetListVideoSuccess,
  onHeadsetRemoved,
  onHeadsetAdded
} from "./actions";

import {
  ADD_HEADSET,
  ADD_HEADSET_SUCCESS,
  LIST_HEADSETS_ALL,
  REMOVE_HEADSET,
  UPLOAD_VIDEO,
  LIST_HEADSETS_VIDEOS,
  LIST_VIDEOS_ALL,
  ADD_VIDEO_IN_HEADSET,
  DELETE_VIDEO,
  PLAY_VIDEO,
  TEST_FIRE
} from "../actions";

export function* watchListAllHeadsets() {
  yield takeEvery(LIST_HEADSETS_ALL, listAllHeadsets);
}

const listAllHeadsetsAsync = async () =>
  await database
  .ref(localStorage.getItem("user_id") + "/headsets")
  .once("value")
  .then(snapshot => {
    return snapshot.val();
  })
  .catch(error => error);

function* listAllHeadsets() {
  try {
    const listHeadset = yield call(listAllHeadsetsAsync);
    yield put(onHeadsetAllListedSuccess(listHeadset));
  } catch (error) {}
}
export function* watchListAllVideos() {
  yield takeEvery(LIST_VIDEOS_ALL, listAllVideos);
}

const listAllVideosAsync = async () =>
  await database
  .ref(localStorage.getItem("user_id") + "/videos")
  .once("value")
  .then(snapshot => {
    return snapshot.val();
  })
  .catch(error => error);

function* listAllVideos() {
  try {
    const listVideo = yield call(listAllVideosAsync);
    yield put(onVideoAllListedSuccess(listVideo));
  } catch (error) {}
}

export function* watchAddHeadset() {
  yield takeEvery(ADD_HEADSET, updateHeadsets);
}

const updateHeadsetsAsync = async updates =>
  await database
  .ref()
  .update(updates)
  .then(addHeadset => addHeadset)
  .catch(error => error);

function* updateHeadsets({
  payload
}) {
  var newHeadsetKey = database
    .ref()
    .child("headsets")
    .push().key;
  var updates = {};
  var payloadID = {
    ...payload,
    id: newHeadsetKey
  };
  updates[
    localStorage.getItem("user_id") + "/headsets/" + newHeadsetKey
  ] = payloadID;
  try {
    const addHeadset = yield call(updateHeadsetsAsync, updates);
    yield put(onHeadsetAllListed());
  } catch (error) {}
}

export function* watchDeleteHeadset() {
  yield takeEvery(REMOVE_HEADSET, deleteHeadsets);
}

const deleteHeadsetsAsync = async deletePath =>
  await database
  .ref(deletePath)
  .set(null)
  .then(deleteHeadset => deleteHeadset)
  .catch(error => error);

function* deleteHeadsets({
  payload
}) {
  const removePath =
    localStorage.getItem("user_id") + "/headsets/" + payload.removedHeadsetId;
  console.log("payload", payload);
  // console.log("removePath", removePath)

  try {
    const deleteHeadset = yield call(deleteHeadsetsAsync, removePath);
    yield put(onHeadsetAllListed());
  } catch (error) {}
}
export function* watchDeleteVideo() {
  yield takeEvery(DELETE_VIDEO, deleteVideo);
}

const deleteVideoAsync = async (deletePath, headsetID, nVideos) =>
  await database
  .ref(deletePath)
  .set(null)
  .then(decrementVideso(headsetID, nVideos))
  .catch(error => error);
const decrementVideso = async (headsetID, nVideos) => 
  await database
  .ref(localStorage.getItem("user_id") + "/headsets/" + headsetID + "/numberVideos")
  .set(nVideos)
  .then(nvids => nvids)
  .catch(error => error)

function* deleteVideo({
  payload
}) {
  const removePath =
    localStorage.getItem("user_id") +
    "/videos/" +
    payload.headsetID +
    "/" +
    payload.videoID;
  const listPath =
    localStorage.getItem("user_id") + "/videos/" + payload.headsetID;
  console.log("payload", payload);
  // console.log("removePath", removePath)
  var nVideos = payload.nVideos;
  nVideos--;

  try {
    const deleteVideo = yield call(deleteVideoAsync, removePath, payload.headsetID, nVideos);
    yield put(onHeadsetListVideo(listPath, payload.headsetID));
  } catch (error) {}
}

export function* watchPlayVideo() {
  yield takeEvery(PLAY_VIDEO, playVideo);
}

const playVideoAsync = async (videoPath, updateState) =>
  await database
  .ref(videoPath)
  .set(updateState)
  .then(playVideo => playVideo)
  .catch(error => error);

function* playVideo({
  payload
}) {
  const videoPath =
    localStorage.getItem("user_id") +
    "/videos/" +
    payload.headsetID +
    "/" +
    payload.videoID +
    "/status/";
  const listPath =
    localStorage.getItem("user_id") + "/videos/" + payload.headsetID;
  console.log("payload", payload);
  const playUpdateState = !payload.currentState;
  // console.log("removePath", removePath)

  try {
    const playVideo = yield call(playVideoAsync, videoPath, playUpdateState);
    yield put(onHeadsetListVideo(listPath, payload.headsetID));
  } catch (error) {}
}

export function* watchUploadVideo() {
  yield takeEvery(UPLOAD_VIDEO, UploadVideo);
}

const UploadVideThumbAsync = async (uploadImg, uploadInfo, nVideos, headsetID) =>
  await uploadImg.then(UploadVideoInfoAsync(uploadInfo, nVideos, headsetID)).catch(error => error);

const UploadVideoInfoAsync = async (uploadInfo, nVideos, headsetID) =>
  await database
  .ref()
  .update(uploadInfo)
  .then(UpdateNumberOfVideos(nVideos, headsetID))
  .catch(error => error);
const UpdateNumberOfVideos = async (nVideos, headsetID) =>
  await database
  .ref(localStorage.getItem("user_id") + "/headsets/" + headsetID + "/numberVideos")
  .set(nVideos)
  .then(nvids => nvids)
  .catch(error => error);

function* UploadVideo({
  payload
}) {
  var newVideoKey = database
    .ref()
    .child("videos")
    .push().key;
  var fileResizedName = payload.file.name;
  var fileResizedNameArr = fileResizedName.split(".");
  fileResizedNameArr[0] = fileResizedNameArr[0] + "_200x200";
  fileResizedName = fileResizedNameArr.join(".");
  var fileNameWithoutResize =
    localStorage.getItem("user_id") + newVideoKey + payload.file.name;
  var fileName =
    localStorage.getItem("user_id") + newVideoKey + fileResizedName;

  var fileNamewithURL =
    "https://firebasestorage.googleapis.com/v0/b/digimedical.appspot.com/o/" +
    fileNameWithoutResize +
    "?alt=media&token=14e5d94f-8959-4da8-b9c2-5b96c52f744d";
  var uploadInfo = {};
  var payloadID = {
    ...payload.videoInfo,
    id: newVideoKey,
    thumbPath: fileNamewithURL
  };
  var videosRef =
    localStorage.getItem("user_id") + "/videos/" + payload.videoInfo.headsetID;
  uploadInfo[videosRef + "/" + newVideoKey] = payloadID;

  var storageRef = storage.ref();
  var uploadTask = storageRef.child(fileNameWithoutResize).put(payload.file);
  var nVideos = payload.nVideos;
  nVideos++;
  console.log("function*UploadVideo -> nVideos", nVideos)

  try {
    const uploadVideo = yield call(
      UploadVideThumbAsync,
      uploadTask,
      uploadInfo,
      nVideos,
      payloadID.headsetID
    );
    yield put(onHeadsetListVideo(videosRef, payload.videoInfo.headsetID));
  } catch (error) {}
}


export function* watchListHeadsetVideos() {
  yield takeEvery(LIST_HEADSETS_VIDEOS, listHeadsetVideos);
}

const listHeadsetVideosAsync = async videosRef =>
  await database
  .ref(videosRef)
  .once("value")
  .then(snapshot => {
    return snapshot.val();
  })
  .catch(error => error);

function* listHeadsetVideos({
  payload
}) {
  var videosRef = payload.videosRef;

  try {
    const listVideoHeadset = yield call(listHeadsetVideosAsync, videosRef);
    yield put(onHeadsetListVideoSuccess(payload.headsetID, listVideoHeadset));
  } catch (error) {}
}

export default function* rootSaga() {
  yield all([
    fork(watchAddHeadset),
    fork(watchListAllHeadsets),
    fork(watchListAllVideos),
    fork(watchDeleteHeadset),
    fork(watchDeleteVideo),
    fork(watchUploadVideo),
    fork(watchListHeadsetVideos),
    fork(watchPlayVideo)
  ]);
}