import {
  ADD_HEADSET,
  LIST_HEADSETS_ALL,
  LIST_HEADSETS_ALL_SUCCESS,
  ADD_HEADSET_SUCCESS,
  ADD_HEADSET_ERROR,
  SHOW_ADD_VIDEO,
  HIDE_ADD_VIDEO,
  UPLOAD_VIDEO,
  LIST_HEADSETS_VIDEOS,
  LIST_VIDEOS_ALL,
  LIST_VIDEOS_ALL_SUCCESS,
  LIST_HEADSETS_VIDEOS_SUCCESS,
  DROP_VIDEO_THUMBNAIL,
  ADD_VIDEO_IN_HEADSET,
  ADD_VIDEO_IN_HEADSET_SUCCESS,
  ADD_VIDEO_IN_HEADSET_ERROR,
  REMOVE_HEADSET,
  PLAY_VIDEO,
  DELETE_VIDEO,
  TEST_FIRE
} from "../actions";

export const onHeadsetAdded = (id, headsetName, status, numberVideos, numberApss, deleted, headsetKey) => ({
  type: ADD_HEADSET,
  payload: { id, headsetName, status, numberVideos, numberApss, deleted, headsetKey }
});
export const onHeadsetAddedSuccess = (allHeadsets) => ({
  type: ADD_HEADSET_SUCCESS,
  payload: allHeadsets 
});

export const onHeadsetAllListed = () => ({
  type: LIST_HEADSETS_ALL
});
export const onVideoAllListed = () => ({
  type: LIST_VIDEOS_ALL
});
export const onHeadsetAllListedSuccess = (allHeadsets) => ({
    type: LIST_HEADSETS_ALL_SUCCESS,
    payload : allHeadsets
});
export const onVideoAllListedSuccess = (allVideos) => ({
    type: LIST_VIDEOS_ALL_SUCCESS,
    payload : allVideos
});

export const onShowAddVideoView = () => ({
  type: SHOW_ADD_VIDEO
})
export const onHideAddVideoView = () => ({
  type: HIDE_ADD_VIDEO
})

export const onThumbVideo = (videoInfo, file, nVideos) => ({
  type: UPLOAD_VIDEO,
  payload : {videoInfo, file, nVideos}
})
export const onHeadsetListVideo = (videosRef, headsetID) => ({
  type: LIST_HEADSETS_VIDEOS,
  payload : {videosRef, headsetID}
})
export const onHeadsetListVideoSuccess = (headsetID, videos) => ({
  type: LIST_HEADSETS_VIDEOS_SUCCESS,
  payload: {headsetID, videos}
})

// export const onThumbVideoUpload = (headsetID, file) => ({
//     type: UPLOAD_VIDEO_THUMBNAIL,
//     payload : {headsetID, file}
// })

export const onVideoAdded = (
  videoId,
  videoTitle,
  videoPath,
  videoDescription,
  thumb,
  date,
  headsetIndex
) => ({
  type: ADD_VIDEO_IN_HEADSET,
  payload: {
    videoId,
    videoTitle,
    videoPath,
    videoDescription,
    thumb,
    date,
    headsetIndex
  }
});

export const onHeadsetRemoved = removedHeadsetId => ({
  type: REMOVE_HEADSET,
  payload: { removedHeadsetId }
});

export const onPlayVideo = (headsetID, videoID, currentState) => ({
  type: PLAY_VIDEO,
  payload: { headsetID, videoID, currentState }
});

export const onDeleteVideo = (headsetID, videoID, nVideos) => ({
  type: DELETE_VIDEO,
  payload: { headsetID, videoID, nVideos }
});

export const onFireTest = () => ({
  type: TEST_FIRE
});
